import React from "react";
import "./Navbar.css";
import logo from "./img/logo.png";

const Navbar = () => {
  return (
    <>
      <div style={{ display: "flex", justifyContent: "space-between" }}>
        <img className="logo" src={logo} alt="LOGO" />

        <ul className="navbar">
          <li>Concept</li>
          <li>Progress</li>
          <li>Stack</li>
          <li>Operation</li>
          <li>Member</li>
          <li className="loginBt">login</li>
        </ul>
      </div>
    </>
  );
};

export default Navbar;
